<template>
  <div class="wrapper">
    <!-- header -->
    <header>
      <Back></Back>
      <p>&emsp;&emsp;&emsp;&emsp;&emsp;&emsp;</p>
      <p></p>
      <p>&emsp;&emsp;&emsp;&emsp;&emsp;&emsp;&emsp;</p>
    </header>
    <!--  -->
    <h3></h3>
    <div class="order-info">
      <p>
        {{ orders.business.businessName }}
        <i class="fa fa-caret-down" @click="detailetShow"></i>
      </p>
      <p>&#165;{{ orders.orderTotal ? orders.orderTotal.toFixed(2) : '0.0' }}</p>
    </div>

    <!--  -->
    <ul class="order-detailet" v-show="isShowDetailet">
      <li v-for="item in orders.list" :key="item.food.foodId">
        <p>{{ item.food.foodName }} x {{ item.quantity }}</p>
        <p>&#165;{{ (item.food.foodPrice * item.quantity).toFixed(2) }}</p>
      </li>
      <li>
        <p></p>
        <p>&#165;{{ orders.business.deliveryPrice ? orders.business.deliveryPrice.toFixed(2) : '0.0' }}</p>
      </li>
      <li>
        <p></p>
        <p>&#165;{{ -(this.totalpoints * 0.1 > this.orders.orderTotal ? orders.orderTotal : this.totalpoints * 0.1).toFixed(1) }}</p>
      </li>
    </ul>

    <!--  -->
    <ul class="payment-type">
      <li>
        <!-- <img src=""> -->
        
        <i class="fa fa-check-circle"></i>
      </li>
      <li>
        <img src="../assets/alipay.png">
        <!-- <i class="fa fa-check-circle"></i> -->
      </li>
      <li>
        <img src="../assets/wechat.png">
      </li>
    </ul>
    <div class="payment-button">
      <button @click="handlePayment"></button>
    </div>

    <!--  -->
    <Footer></Footer>
  </div>
</template>
<script>
	import Footer from '../components/Footer.vue';
  import Back from "@/components/Back.vue";

	export default {
		name: 'Payment',
		data(){
			return {
				orderId:this.$route.query.orderId,
				orders:{
					business:{}
				},
				isShowDetailet:false
			}
		},
		created() {
      this.$axios.post('OrdersController/getOrdersById',this.$qs.stringify({
        orderId:this.orderId
      })).then(response=>{
        this.orders = response.data;
      }).catch(error=>{
        console.error(error);
      });
    },
		mounted() {
			//
			//urlhistory
			history.pushState(null,null,document.URL);
			//popstatehistory
			window.onpopstate = () => {
				this.$router.push({path:'/index'});
			}
		},
		destroyed() {
			window.onpopstate = null;
		},
		methods:{
      toSuccess(){
        this.$router.push({
          path: '/paySuccess',

        });
      },
      handlePayment() {
        this.$axios.post('OrdersController/payOrder', this.$qs.stringify({
          orderId: this.orderId
        })).then(response => {
          if (response.data > 0) {
            // 
            this.orders.status = '';
            // 
            this.$router.push({ path: '/paySuccess' });
          } else {
            alert('');
          }
        }).catch(error => {
          console.error(error);
          alert('');
        });
      },
			detailetShow(){
				this.isShowDetailet = !this.isShowDetailet;
			}
		},
		components: {
      Back,
			Footer
		}
	}
</script>

<style scoped>
	/******************  ******************/
	.wrapper {
		width: 100%;
		height: 100%;
	}

	/****************** header ******************/
	.wrapper header {
		width: 100%;
		height: 12vw;
		background-color: #0097FF;
		color: #fff;
		font-size: 4.8vw;

		position: fixed;
		left: 0;
		top: 0;
		z-index: 1000;

		display: flex;
		justify-content: center;
		align-items: center;
	}

	/******************  ******************/
	.wrapper h3 {
		margin-top: 12vw;
		box-sizing: border-box;
		padding: 4vw 4vw 0;

		font-size: 4vw;
		font-weight: 300;
		color: #999;
	}

	.wrapper .order-info {
		box-sizing: border-box;
		padding: 4vw;
		font-size: 4vw;
		color: #666;

		display: flex;
		justify-content: space-between;
		align-items: center;
	}

	.wrapper .order-info p:last-child {
		color: orangered;
	}

	/******************  ******************/
	.wrapper .order-detailet {
		width: 100%;
	}

	.wrapper .order-detailet li {
		width: 100%;
		box-sizing: border-box;
		padding: 1vw 4vw;

		display: flex;
		justify-content: space-between;
		align-items: center;
	}

	.wrapper .order-detailet li p {
		font-size: 3vw;
		color: #666;
	}

	/******************  ******************/
	.wrapper .payment-type {
		width: 100%;
	}

	.wrapper .payment-type li {
		width: 100%;
		box-sizing: border-box;
		padding: 4vw;

		display: flex;
		justify-content: space-between;
		align-items: center;
    color: #666;
	}

	.wrapper .payment-type li img {
		width: 33vw;
		height: 8.9vw;
	}

	.wrapper .payment-type li .fa-check-circle {
		font-size: 5vw;
		color: #38CA73;
	}

	.wrapper .payment-button {
		width: 100%;
		box-sizing: border-box;
		padding: 4vw;
	}

	.wrapper .payment-button button {
		width: 100%;
		height: 10vw;
		border: none;
		/**/
		outline: none;
		border-radius: 4px;
		background-color: #38CA73;
		color: #fff;
	}
</style>
