<template>
  <div class="wrapper">
    <!-- header -->
    <header>
      <Back>
      </Back>
      <p>&emsp;&emsp;&emsp;&emsp;&emsp;&emsp;</p >
      <p></p >
      <p>&emsp;&emsp;&emsp;&emsp;&emsp;&emsp;&emsp;</p >
    </header>
    <!--  -->
    <ul class="form-box">
      <li>
        <div class="title">
          
        </div>
        <div class="content">
          {{user.userName}}
        </div>
      </li>
      <li>
        <div class="title">
          
        </div>
        <div class="content">
          <input v-model="user.oldPassword" type="password" placeholder="" @blur="validateOldPassword">
        </div>
      </li>
      <li>
        <div class="title">
          
        </div>
        <div class="content">
          <input v-model="user.new1Password" type="password" placeholder="" @blur="validateNewPassword">
        </div>
      </li>
      <li>
        <div class="title">
          
        </div>
        <div class="content">
          <input v-model="user.new2Password" type="password" placeholder="" @blur="validateConfirmPassword">
        </div>
      </li>
    </ul>
    <div class="button-login">
      <button @click="updatePwd()"></button>
    </div>
    <!--  -->
    <Footer></Footer>
  </div>
</template>

<script>
import Footer from '../components/Footer.vue';
import Back from "@/components/Back.vue";

export default {
  name: 'Register',
  components: {
    Back,
    Footer
  },
  data() {
    return {
      user: {
        userId: '',
        userName: '',
        password: '',
        oldPassword: '',
        new1Password: '',
        new2Password: '',
      }
    }
  },
  created() {
    this.user = this.$getSessionStorage("user");
  },
  methods: {
    validateOldPassword() {
      if (!this.user.oldPassword) {
        alert("!");
      }
    },
    validateNewPassword() {
      const passwordRegex = /^(?=.*[A-Za-z])(?=.*\d)[A-Za-z\d]{6,}$/;
      if (!passwordRegex.test(this.user.new1Password)) {
        alert('6');
      }
    },
    validateConfirmPassword() {
      if (this.user.new1Password !== this.user.new2Password) {
        alert("!");
      }
    },
    updatePwd() {
      let user = this.user;
      // 1. 
      if (!user.oldPassword || !user.new1Password || !user.new2Password) {
        alert("!");
        return;
      }

      // 2. 
      const passwordRegex = /^(?=.*[A-Za-z])(?=.*\d)[A-Za-z\d]{6,}$/;
      if (!passwordRegex.test(user.new1Password)) {
        alert('6');
        return;
      }

      // 4. 
      if (user.oldPassword === user.new1Password) {
        alert("!");
        return;
      }

      // 5. 
      if (user.new1Password !== user.new2Password) {
        alert("!");
        return;
      }

      //  Promise 
      this.$axios.post('UserController/modifyPassword', this.$qs.stringify({
        userId: this.$route.query.userId,
        oldPassword: this.oldPassword,
        Password: user.Password
      }))
          .then(resp => {
            let result = resp.data;
            console.log('data:', result);
            if (resp.data == true) {
              this.$message.success("");
            } else {
              this.$message.success('');
              this.$router.go(-1);
            }
          })
          .catch(err => {
            console.log(err);
            this.$message.success('');
          });
    }
  }
}
</script>

