<template>
  <div class="wrapper">
    <header>
      <Back>
      </Back>
      <p>&emsp;&emsp;&emsp;&emsp;&emsp;&emsp;</p >
      <p></p >
      <p>&emsp;&emsp;&emsp;&emsp;&emsp;&emsp;&emsp;</p >
    </header>
    <div class="content">
      <input type="text" v-model="businessId" placeholder="businessId">
      <button @click="verifyBusinessId"></button>
    </div>
  </div>
</template>

<script>
import Back from "@/components/Back.vue";

export default {
  name: 'BecomeMerchant',
  components: {Back},
  data() {
    return {
      businessId: ''
    }
  },
  methods: {
    verifyBusinessId() {
      // userIdpassword
      let user = this.$getSessionStorage('user');
      console.log('User from session storage:', user); // 

      if (!user) {
        this.$message.error('');
        console.log('User is not logged in'); // 
        return;
      }

      let userId = user.userId;
      console.log('User ID:', userId); // 

      //  businessId 
      console.log('Business ID:', this.businessId); // 

      // 
      this.$axios.post('BusinessController/verifyBusinessId', this.$qs.stringify({
        userId: userId,
        businessId: this.businessId
      })).then(resp => {
        console.log('Response from server:', resp); // 

        if (resp.data == true) {
          this.$message.success('');
          console.log('Verification successful, redirecting to business control page'); // 
          this.$router.push({
            path: '/businessControl',
            query: {
              businessId: this.businessId
            }
          });
        } else {
          this.$message.error('');
          console.log('Verification failed'); // 
        }
      }).catch(err => {
        console.log('Error during verification:', err); // 
        this.$message.error('');
      });
    }
  }
}
</script>

<style scoped>
/***************  ***************/
.wrapper {
  width: 100%;
  height: 100%;
  background-color: #F5F5F5;
}

/*************** header ***************/
.wrapper header {
  width: 100%;
  height: 12vw;
  background-color: #0097FF;
  color: #fff;
  font-size: 4.8vw;
  position: fixed;
  left: 0;
  top: 0;
  z-index: 1000;
  display: flex;
  justify-content: center;
  align-items: center;
}

/*************** content ***************/
.wrapper .content {
  width: 100%;
  padding-top: 12vw;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.wrapper .content input {
  width: 80%;
  height: 10vw;
  margin-top: 5vw;
  padding: 0 2vw;
  font-size: 4vw;
  border: 1px solid #ccc;
  border-radius: 5px;
}

.wrapper .content button {
  width: 80%;
  height: 10vw;
  margin-top: 5vw;
  background-color: #0097FF;
  color: #fff;
  font-size: 4vw;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.wrapper .content button:hover {
  background-color: #007ACC;
}
</style>