<template>
  <div class="wrapper">
    <heade>
      <p></p>
    </heade>
    <!-- header -->
    <header>
      <img :src="user.userImg" @error="handleImageError" alt="User avatar">
      <p>{{ user.userName }}</p>
    </header>

    <!--  -->
    <ul class="userinfoa">
      <li @click="changeImg(user.userId)">
        <img src="../assets/user.png"><p></p>
      </li>
      <li @click="toMyWallet">
        <img src="../assets/wallet.png"><p></p>
      </li>
      <li @click="toMyCredit">
        <img src="../assets/points.jpg"><p></p>
      </li>
    </ul>
    <ul class="userinfob">
      <li @click="changePwd">
        <p><i class="fa fa-expeditedssl"></i></p>
        <i class="fa fa-angle-right"></i>
      </li>
      <li>
        <p><i class="fa fa-gratipay"></i></p>
        <i class="fa fa-angle-right"></i>
      </li>
      <li @click="toBecomeMerchant">
        <p><i class="fa fa-sign-out"></i></p>
        <i class="fa fa-angle-right"></i>
      </li>
      <li @click="logOut()">
        <p><i class="fa fa-sign-out"></i></p>
        <i class="fa fa-angle-right"></i>
      </li>

    </ul>
    <Footer></Footer>
  </div>
</template>

<script>
import Footer from '../components/Footer.vue'
export default {
  name: 'MyInfo',
  data: function() {
    return {
      user: {
        userImg: require('../assets/user.png') // 
      }
    }
  },
  methods: {
    // 
    changePwd() {
      this.$router.push('/changePwd');
    },
    // 
    changeImg: function(userId) {
      this.$router.push({
        path: '/myImg',
        query: {
          userId: userId
        }
      });
    },
    toMyCredit: function() {
      this.$router.push('/credit');
    },
    toMyWallet: function() {
      this.$router.push('/myWallet');
    },
    toBecomeMerchant: function() {
      this.$router.push('/becomeMerchant');
    },
    // 
    getUserById: function(userId) {
      let that = this;
      let url = 'UserController/getUserById';
      this.$axios.post(url, this.$qs.stringify({
        userId
      }))
          .then(resp => {
            that.user = resp.data;
            if (that.user.userImg) {
              that.user.userImg = that.user.userImg; // 
            }
          })
          .catch(err => {
            console.log(err);
          })
    },
    //  (,)
    logOut(user) {
      this.$removeSessionStorage('user');
      let result = this.$getSessionStorage('user');
      if (result == null) {
        alert('');
        this.$router.push('/index');
      } else {
        alert('');
      }
    },
    // 
    handleImageError() {
      this.user.userImg = require('../assets/user.png'); // 
    }
  },
  components: {
    Footer: Footer
  },
  created: function() {
    let user = this.$getSessionStorage('user');
    if (user != null) {
      this.getUserById(user.userId);
    }
  }
}
</script>

